<template>
  <div id="cy" style="width: 100%;height: 100%;"></div>
</template>
<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
// import dagre from 'cytoscape-dagre';

Vue.use(VueAxios, axios);

var cytoscape = require("cytoscape");
var dagre = require("cytoscape-dagre");
cytoscape.use(dagre); // register extension

export default {
  name: "Cytoscape",
  components: {},
  created: function() {},
  data: function() {
    return {
      input: "",
      output: "",
      msg: "vue to cytoscape",
      count: 0,
      edges: "",
      nodes: "node"
    };
  },
  methods: {
    add_node: function() {
      console.info(this.cy);
      this.cy.add([
        {
          group: "nodes",
          data: { id: "node" + this.count },
          position: { x: 300, y: 200 }
        },
        {
          group: "edges",
          data: {
            id: "edge" + this.count,
            source: "node" + this.count,
            target: "cat"
          }
        }
      ]);
    },
    view_init: function() {
      this.cy = cytoscape({
        container: document.getElementById("cy"),
        boxSelectionEnabled: false,
        autounselectify: true,
        style: cytoscape
          .stylesheet()
          .selector("node")
          .css({
            "background-color": "#555",
            color: "#fff",
            content: "data(id)",
            "font-size": "22px",
            // height: "mapData(score, 0, 0.006769776522008331, 20, 60)",
            height: "150px",
            width: "150px",
            "overlay-padding": "6px",
            "text-halign": "center",
            "text-outline-color": "#555",
            "text-outline-width": "2px",
            "text-valign": "center",
            // width: "mapData(score, 0, 0.006769776522008331, 20, 60)",
            "z-index": "10",
            "text-overflow": "ellipsis",
            overflow: "hidden"
          })
          .selector("edge")
          .css({
            width: 6,
            "target-arrow-shape": "triangle",
            "line-color": "#ffaaaa",
            "target-arrow-color": "#ffaaaa",
            "curve-style": "bezier",
            "z-index": "1111"
          })
          .selector(".company")
          .css({
           "background-color": "#555",
            color: "#fff",
          })
          .selector(".investor")
          .css({
            "background-color": "#032892",
            color: "#fff",
            "border-width":"0px",
            "border-style":"solid",
            "border-color":"yellow"
          }),

        elements: {
          nodes: this.nodes,
          edges: this.edges
        }
        // layout: {
        //   name: "breadthfirst",
        //   directed: true,
        //   padding: 10
        // }
      });

      // let options = {
      //   name: "grid",

      //   fit: true, // whether to fit the viewport to the graph
      //   directed: false, // whether the tree is directed downwards (or edges can point in any direction if false)
      //   padding: 30, // padding on fit
      //   circle: true, // put depths in concentric circles if true, put depths top down if false
      //   grid: false, // whether to create an even grid into which the DAG is placed (circle:false only)
      //   spacingFactor: 3.75, // positive spacing factor, larger => more space between nodes (N.B. n/a if causes overlap)
      //   boundingBox: undefined, // constrain layout bounds; { x1, y1, x2, y2 } or { x1, y1, w, h }
      //   avoidOverlap: true, // prevents node overlap, may overflow boundingBox if not enough space
      //   nodeDimensionsIncludeLabels: false, // Excludes the label when calculating node bounding boxes for the layout algorithm
      //   roots: undefined, // the roots of the trees
      //   maximal: false, // whether to shift nodes down their natural BFS depths in order to avoid upwards edges (DAGS only)
      //   animate: false, // whether to transition the node positions
      //   animationDuration: 500, // duration of animation in ms if enabled
      //   animationEasing: undefined, // easing of animation if enabled,
      //   animateFilter: function(node, i) {
      //     return true;
      //   }, // a function that determines whether the node should be animated.  All nodes animated by default on animate enabled.  Non-animated nodes are positioned immediately when the layout starts
      //   ready: undefined, // callback on layoutready
      //   stop: undefined, // callback on layoutstop
      //   transform: function(node, position) {
      //     return position;
      //   } // transform a given node position. Useful for changing flow direction in discrete layouts
      // };
      var options = {
        name: "dagre",
        // dagre algo options, uses default value on undefined
        nodeSep: 100, // the separation between adjacent nodes in the same rank
        edgeSep: 100, // the separation between adjacent edges in the same rank
        rankSep: 400, // the separation between adjacent nodes in the same rank
        rankDir: "TB", // 'TB' for top to bottom flow, 'LR' for left to right,
        ranker: "network-simplex", // Type of algorithm to assign a rank to each node in the input graph. Possible values: 'network-simplex', 'tight-tree' or 'longest-path'
        minLen: function(edge) {
          return 1;
        }, // number of ranks to keep between the source and target of the edge
        edgeWeight: function(edge) {
          return 1;
        }, // higher weight edges are generally made shorter and straighter than lower weight edges

        // general layout options
        fit: true, // whether to fit to viewport
        padding: 30, // fit padding
        spacingFactor: undefined, // Applies a multiplicative factor (>0) to expand or compress the overall area that the nodes take up
        nodeDimensionsIncludeLabels: false, // whether labels should be included in determining the space used by a node
        animate: false, // whether to transition the node positions
        animateFilter: function(node, i) {
          return true;
        }, // whether to animate specific nodes when animation is on; non-animated nodes immediately go to their final positions
        animationDuration: 500, // duration of animation in ms if enabled
        animationEasing: undefined, // easing of animation if enabled
        boundingBox: undefined, // constrain layout bounds; { x1, y1, x2, y2 } or { x1, y1, w, h }
        transform: function(node, pos) {
          return pos;
        }, // a function that applies a transform to the final node position
        ready: function() {}, // on layoutready
        stop: function() {} // on layoutstop
      };

      var layout = this.cy.layout(options);
      layout.run();
    },
    get: function() {
      // 发送get请求
      const that = this;
      this.$http
        .get("http://maogou.pagecp.com/index.php/api/all_investor")
        .then(
          function(res) {
            var data = res.data.d;
            that.nodes = data.nodes;
            that.edges = data.edges;
            that.view_init();
          },
          function() {
            console.log("请求失败处理");
          }
        );
    }
  },
  computed: {},
  mounted: function() {},
  created: function() {
    this.get();
  }
};
</script>
<style scoped>
#cy {
  width: 100%;
  height: 80%;
  position: absolute;
  top: 50px;
  left: 0px;
  text-align: left;
}
.aa {
  color: red;
}

body {
